<template>
	<view class="box">
		<image lazy-load class="top-img" src="../../static/images/newpeople_banner.png" mode="aspectFill"></image>
		<view class="top-tips-box">
			<view class="iconfont iconxinrenfuli-tongzhi">
			</view>
			超值福利赠送，团批劵可以免费在团批专区兑换商品哦！
		</view>
		<!-- 专区 -->
		<areaIcon :curr-page="'area_new_people'"></areaIcon>
		<!-- 分类商品 -->
		<view class="good-classified">

			<!-- 商品列表 -->
			<view class="goods">
				<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption" :down="{'use':false}">
					<view class="goods-box">
						<navigator v-for="(item,k) in data_list" class="goods-li" :url="'/pages/goods/new_people_detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId"
						 :key="k">
							<image lazy-load class="img scale45" :src="item.mainPicUrl" mode="aspectFill"></image>
							<view class="title-box">
								<view class="tips-box" v-if="data_list[k].spuLabel">
									<template v-for="(val,i) in data_list[k].spuLabel">
										<view class="width106 " :class="{'bg-btn':i==0,'border-btn':i==1}" :key="i">
											{{val}}
										</view>
									</template>
								</view>
								<view class="title">
									{{item.spuTitle}}
								</view>
								<view class="price-box">
									<view class="num font-bold">￥{{item.promptPrice | formatNum}}</view>
									<view class="group_reward" v-if="item.o2oMarketingRetCoupon">送{{item.o2oMarketingRetCoupon.couponName}}</view>
								</view>
							</view>
						</navigator>
					</view>
				</mescroll-body>
			</view>
		</view>


	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	import areaIcon from './components/area-icon.vue'
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				data_list: [],
				upOption: {
					page: {
						size: 6
					},
					textNoMore: "没有更多数据",
				},
			}
		},
		components: {
			areaIcon,
			MescrollBody
		},
		onLoad() {
			uni.showLoading({
				title: '加载中'
			});
			setTimeout(function() {
				uni.hideLoading()
			}, 600)
		},
		methods: {
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oSpu/findHeadPage', {
					params: {
						pageno: pageNum,
						pagesize: pageSize,
						promptType: 'RET_COUPON',//促销类型
						purchaseType: 'ZC', //购买类型
						onSale: 'SALE'
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;
					for (let val of curPageData) {
						if (val.spuLabel) {
							val.spuLabel = val.spuLabel.split(',')
						}
					}
					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			}
		}

	}
</script>

